export class PeerTubeEmbed {
  playerElement: HTMLVideoElement;
  player: any;
  api: PeerTubeEmbedApi = null;
  host: string;
  language: string;
  autoplay: boolean;
  wautoplay: boolean;
  controls: boolean;
  muted: boolean;
  loop: boolean;
  subtitle: string;
  modeParam: string;
  enableApi = false;
  startTime: number | string = 0;
  stopTime: number | string;
  clbk: any;
  title: boolean;
  warningTitle: boolean;
  peertubeLink: boolean;
  bigPlayBackgroundColor: string;
  foregroundColor: string;
  contributor : string;
  mode: PlayerMode;

  scope = "peertube";

  userTokens: Tokens;
  headers = new Headers();
  LOCAL_STORAGE_OAUTH_CLIENT_KEYS = {
    CLIENT_ID: "client_id",
    CLIENT_SECRET: "client_secret",
  };

  isVideoEmbed: string;
  txid: string;

  private translationsPromise: Promise<{ [id: string]: string }>;
  private configPromise: Promise<ServerConfig>;
  //private PeertubePlayerManagerModulePromise: Promise<any>

  private playlist: VideoPlaylist;
  private playlistElements: VideoPlaylistElement[];
  private currentPlaylistElement: VideoPlaylistElement;

  private wrapperElement: HTMLElement;

  private peertubeHooks: Hooks = {};
  private loadedScripts = new Set<string>();

  static async main(
    element: HTMLElement,
    videoId: string,
    parameters: any,
    clbk: any
  ) {

    const embed = new PeerTubeEmbed(element);


    embed.getInitialSettings(parameters);
    await embed.init(videoId, parameters, clbk);


    return embed;
  }

  constructor(private element: HTMLElement) {
    this.wrapperElement = element;
  }

  getInitialSettings(params: any) {
    //const params = new URL(window.location.toString()).searchParams



    this.host = "https://" + this.getParamString(params, "host", "");
    this.contributor = this.getParamString(params, "contributor", "peertube");
    this.language = this.getParamString(params, "language", navigator.language);
    this.autoplay = this.getParamToggle(params, "autoplay", false);
    this.wautoplay = this.getParamToggle(params, "wautoplay", false);
    this.controls = this.getParamToggle(params, "controls", true);
    this.muted = this.getParamToggle(params, "muted", undefined);
    this.loop = this.getParamToggle(params, "loop", false);
    this.title = this.getParamToggle(params, "title", false);
    this.enableApi = this.getParamToggle(params, "api", this.enableApi);
    this.warningTitle = this.getParamToggle(params, "warningTitle", false);
    this.peertubeLink = this.getParamToggle(params, "peertubeLink", false);
    this.scope = this.getParamString(params, "scope", this.scope);
    this.subtitle = this.getParamString(params, "subtitle");
    this.startTime = this.getParamString(params, "start");
    this.stopTime = this.getParamString(params, "stop");
    this.bigPlayBackgroundColor = this.getParamString(
      params,
      "bigPlayBackgroundColor"
    );
    this.foregroundColor = this.getParamString(params, "foregroundColor");
    this.modeParam = this.getParamString(params, "mode");

    this.isVideoEmbed = this.getParamString(params, "videoEmbedded", "");
    this.txid = this.getParamString(params, "txid", "");

  }

  getVideoUrl(id: string) {
    return "/api/v1/videos/" + id;
  }

  refreshFetch(url: string, options?: RequestInit) {
    return fetch(this.host + url, options).then((res: Response) => {
      if (res.status !== HttpStatusCode.UNAUTHORIZED_401) return res;

      const refreshingTokenPromise = new Promise<void>((resolve, reject) => {
        const clientId: string = peertubeLocalStorage.getItem(
          this.LOCAL_STORAGE_OAUTH_CLIENT_KEYS.CLIENT_ID
        );
        const clientSecret: string = peertubeLocalStorage.getItem(
          this.LOCAL_STORAGE_OAUTH_CLIENT_KEYS.CLIENT_SECRET
        );

        const headers = new Headers();
        headers.set("Content-Type", "application/x-www-form-urlencoded");

        const data = {
          refresh_token: this.userTokens.refreshToken,
          client_id: clientId,
          client_secret: clientSecret,
          response_type: "code",
          grant_type: "refresh_token",
        };

        fetch(this.host + "/api/v1/users/token", {
          headers,
          method: "POST",
          body: objectToUrlEncoded(data),
        })
          .then((res) => {
            if (res.status === HttpStatusCode.UNAUTHORIZED_401)
              return undefined;

            return res.json();
          })
          .then((obj: UserRefreshToken & { code: "invalid_grant" }) => {
            if (!obj || obj.code === "invalid_grant") {
              Tokens.flush();
              this.removeTokensFromHeaders();

              return resolve();
            }

            this.userTokens.accessToken = obj.access_token;
            this.userTokens.refreshToken = obj.refresh_token;
            this.userTokens.save();

            this.setHeadersFromTokens();

            resolve();
          })
          .catch((refreshTokenError: any) => {
            reject(refreshTokenError);
          });
      });

      return refreshingTokenPromise
        .catch(() => {
          Tokens.flush();

          this.removeTokensFromHeaders();
        })
        .then(() =>
          fetch(url, {
            ...options,
            headers: this.headers,
          })
        );
    });
  }

  getPlaylistUrl(id: string) {
    return "/api/v1/video-playlists/" + id;
  }

  loadVideoInfo(videoId: string): Promise<Response> {
    return this.refreshFetch(this.getVideoUrl(videoId), {
      headers: this.headers,
    });
  }

  loadVideoCaptions(videoId: string): Promise<Response> {
    return this.refreshFetch(this.getVideoUrl(videoId) + "/captions", {
      headers: this.headers,
    });
  }

  loadPlaylistInfo(playlistId: string): Promise<Response> {
    return this.refreshFetch(this.getPlaylistUrl(playlistId), {
      headers: this.headers,
    });
  }

  loadPlaylistElements(playlistId: string, start = 0): Promise<Response> {
    const url = new URL(this.getPlaylistUrl(playlistId) + "/videos");
    url.search = new URLSearchParams({
      start: "" + start,
      count: "100",
    }).toString();

    return this.refreshFetch(url.toString(), { headers: this.headers });
  }

  loadConfig(): Promise<ServerConfig> {
    return this.refreshFetch("/api/v1/config").then((res) => res.json());
  }

  removeElement(element: HTMLElement) {
    element.parentElement.removeChild(element);
  }

  displayError(text: string, translations?: Translations) {
    // Remove video element
    if (this.playerElement) {
      this.removeElement(this.playerElement);
      this.playerElement = undefined;
    }

    const translatedText = peertubeTranslate(text, translations);
    const translatedSorry = peertubeTranslate("Sorry", translations);

    ///document.title = translatedSorry + ' - ' + translatedText

    const errorBlock = document.getElementById("error-block");
    errorBlock.style.display = "flex";

    const errorTitle = document.getElementById("error-title");
    errorTitle.innerHTML = peertubeTranslate("Sorry", translations);

    const errorText = document.getElementById("error-content");
    errorText.innerHTML = translatedText;

    this.wrapperElement.style.display = "none";
  }

  videoNotFound(translations?: Translations) {
    const text = "This video does not exist.";
    this.displayError(text, translations);
  }

  videoFetchError(translations?: Translations) {
    const text = "We cannot fetch the video. Please try again later.";
    this.displayError(text, translations);
  }

  playlistNotFound(translations?: Translations) {
    const text = "This playlist does not exist.";
    this.displayError(text, translations);
  }

  playlistFetchError(translations?: Translations) {
    const text = "We cannot fetch the playlist. Please try again later.";
    this.displayError(text, translations);
  }

  getParamToggle(params: any, name: string, defaultValue?: boolean) {
    return params[name] ? true : defaultValue;
  }

  getParamString(params: any, name: string, defaultValue?: string) {
    return params[name] ? params[name] : defaultValue;
  }

  destroy() {
    this.player.dispose();
  }

  async playNextVideo() {
    const next = this.getNextPlaylistElement();
    if (!next) {
      return;
    }

    this.currentPlaylistElement = next;

    return this.loadVideoAndBuildPlayer(this.currentPlaylistElement.video.uuid);
  }

  async playPreviousVideo() {
    const previous = this.getPreviousPlaylistElement();
    if (!previous) {
      return;
    }

    this.currentPlaylistElement = previous;

    await this.loadVideoAndBuildPlayer(this.currentPlaylistElement.video.uuid);
  }

  getCurrentPosition() {
    if (!this.currentPlaylistElement) return -1;

    return this.currentPlaylistElement.position;
  }

  async init(videoId: string, parameters: any, clbk: any) {
    try {
      this.userTokens = Tokens.load();
      this.clbk = clbk;
      await this.initCore(videoId, parameters);
    } catch (e) {
      console.error(e);
    }
  }

  private initializeApi() {
    if (!this.clbk) return;

    this.api = new PeerTubeEmbedApi(this, this.clbk);
    this.api.initialize();
  }

  private loadParams(video: VideoDetails) {
    try {

      if (this.modeParam) {
        if (this.modeParam === "p2p-media-loader")
          this.mode = "p2p-media-loader";
        else this.mode = "webtorrent";
      } else {
        if (
          Array.isArray(video.streamingPlaylists) &&
          video.streamingPlaylists.length !== 0
        )
          this.mode = "p2p-media-loader";
        else this.mode = "webtorrent";
      }
    } catch (err) {
      console.error("Cannot get params from URL.", err);
    }

  }

  private async loadAllPlaylistVideos(
    playlistId: string,
    baseResult: ResultList<VideoPlaylistElement>
  ) {
    let elements = baseResult.data;
    let total = baseResult.total;
    let i = 0;

    while (total > elements.length && i < 10) {
      const result = await this.loadPlaylistElements(
        playlistId,
        elements.length
      );

      const json = (await result.json()) as ResultList<VideoPlaylistElement>;
      total = json.total;

      elements = elements.concat(json.data);
      i++;
    }

    if (i === 10) {
      console.error("Cannot fetch all playlists elements, there are too many!");
    }

    return elements;
  }

  private async loadPlaylist(playlistId: string) {
    const playlistPromise = this.loadPlaylistInfo(playlistId);
    const playlistElementsPromise = this.loadPlaylistElements(playlistId);

    let playlistResponse: Response;
    let isResponseOk: boolean;

    try {
      playlistResponse = await playlistPromise;
      isResponseOk = playlistResponse.status === HttpStatusCode.OK_200;
    } catch (err) {
      console.error(err);
      isResponseOk = false;
    }

    if (!isResponseOk) {
      const serverTranslations = {}; //await this.translationsPromise

      if (playlistResponse?.status === HttpStatusCode.NOT_FOUND_404) {
        this.playlistNotFound(serverTranslations);
        return undefined;
      }

      this.playlistFetchError(serverTranslations);
      return undefined;
    }

    return { playlistResponse, videosResponse: await playlistElementsPromise };
  }

  private async loadVideo(videoId: string) {
    const videoPromise = this.loadVideoInfo(videoId);

    let videoResponse: Response;
    let isResponseOk: boolean;

    try {
      videoResponse = await videoPromise;
      isResponseOk = videoResponse.status === HttpStatusCode.OK_200;
    } catch (err) {
      console.error(err);

      isResponseOk = false;
    }

    if (!isResponseOk) {
      const serverTranslations = {}; //await this.translationsPromise

      if (videoResponse?.status === HttpStatusCode.NOT_FOUND_404) {
        this.videoNotFound(serverTranslations);
        return undefined;
      }

      this.videoFetchError(serverTranslations);
      return undefined;
    }

    //const captionsPromise = this.loadVideoCaptions(videoId)

    return { /*captionsPromise,*/ videoResponse };
  }

  private async buildPlaylistManager() {
    //const translations = await this.translationsPromise

    this.player.upnext({
      timeout: 10000, // 10s
      headText: peertubeTranslate("Up Next", null),
      cancelText: peertubeTranslate("Cancel", null),
      suspendedText: peertubeTranslate("Autoplay is suspended", null),
      getTitle: () => this.nextVideoTitle(),
      next: () => this.playNextVideo(),
      condition: () => !!this.getNextPlaylistElement(),
      suspended: () => false,
    });
  }

  private async loadVideoAndBuildPlayer(uuid: string) {
    const res = await this.loadVideo(uuid);
    if (res === undefined) return;

    return this.buildVideoPlayer(res.videoResponse);
  }

  private nextVideoTitle() {
    const next = this.getNextPlaylistElement();
    if (!next) return "";

    return next.video.name;
  }

  private getNextPlaylistElement(position?: number): VideoPlaylistElement {
    if (!position) position = this.currentPlaylistElement.position + 1;

    if (position > this.playlist.videosLength) {
      return undefined;
    }

    const next = this.playlistElements.find((e) => e.position === position);

    if (!next || !next.video) {
      return this.getNextPlaylistElement(position + 1);
    }

    return next;
  }

  private getPreviousPlaylistElement(position?: number): VideoPlaylistElement {
    if (!position) position = this.currentPlaylistElement.position - 1;

    if (position < 1) {
      return undefined;
    }

    const prev = this.playlistElements.find((e) => e.position === position);

    if (!prev || !prev.video) {
      return this.getNextPlaylistElement(position - 1);
    }

    return prev;
  }

  private async buildVideoPlayerContributos(
    videoId: string
  ) {

    let alreadyHadPlayer = false;

    if (this.player) {
      this.player.dispose();
      alreadyHadPlayer = true;
    }

    this.playerElement = document.createElement("video");
    this.playerElement.className = "video-js video-js-contributor";
    this.playerElement.setAttribute("playsinline", "true");

    if(this.contributor == 'youtube'){

      var setupYoutube = {
        techOrder : ["youtube"],
        sources : [{ "type": "video/youtube", "src": "https://www.youtube.com/watch?v=" + videoId}],
        youtube: { "iv_load_policy": 1 }
      }

      this.playerElement.setAttribute("data-setup", JSON.stringify(setupYoutube));

    }

    if(this.contributor == 'vimeo'){

      var setupVimeo = {
        techOrder : ["vimeo"],
        sources : [{ "type": "video/vimeo", "src": "https://vimeo.com/" + videoId}]
      }

      this.playerElement.setAttribute("data-setup", JSON.stringify(setupVimeo));

    }

    var paddingSize = 37.5

    this.playerElement.style.cssText = `padding-top: ${paddingSize}%; padding-bottom: ${paddingSize}%;`;

    this.wrapperElement.innerHTML = "";

    this.wrapperElement.appendChild(this.playerElement);

    const options: VideoJsPlayerOptions = {

        autoplay: alreadyHadPlayer ? true : this.autoplay,
        controls: this.controls,
        muted: this.muted,
        loop: this.loop,
        language: this.language
    };

    this.player = await PeertubePlayerManager.initializeContributor(
      this.playerElement,
      options
    );

    this.player.on("customError", (event: any, data: any) =>
      this.handleError(data.err)
    );

    this.initializeApi();

    this.removePlaceholder();

    this.runHook("action:embed.player.loaded", undefined, {
      player: this.player,
      videojs,
      video: {},
    });
  }

  private async buildVideoPlayer(
    videoResponse: Response /*, captionsPromise: Promise<Response>*/
  ) {
    let alreadyHadPlayer = false;

    if (this.player) {
      this.player.dispose();
      alreadyHadPlayer = true;
    }


    const videoInfoPromise = videoResponse
      .json()
      .then(async (videoInfo: VideoDetails) => {
        const metaUrl = videoInfo.files[0].metadataUrl;

        const videoSizeValue = await fetch(metaUrl)
          .then((res: Response) => res.json())
          .then((json: any) => {
            const info = json.streams[0];

            return Number((info.width / info.height));
          });

        this.playerElement = document.createElement("video");
        this.playerElement.className = "video-js";
        this.playerElement.setAttribute("playsinline", "true");

        const isVideoEmbedded = document.querySelector(
          ".standalone-video-embed"
        );

        const paddingSize: Number = 100 / (2 * videoSizeValue);
        if (paddingSize > 50) {
          this.playerElement.classList.add("verticalVideo");

          if (isVideoEmbedded) document.querySelector('.video-js-wrapper').classList.add('vertcalVideoContainer');
        }

        if (!isVideoEmbedded) {
          this.playerElement.style.cssText = `padding-top: ${paddingSize}%; padding-bottom: ${paddingSize}%;`;
        }

        this.wrapperElement.innerHTML = "";

        this.wrapperElement.appendChild(this.playerElement);

        if (!alreadyHadPlayer) this.loadPlaceholder(videoInfo);


        return videoInfo;
      });

    const [
      videoInfoTmp /*serverTranslations, captionsResponse, config, PeertubePlayerManager*/,
    ] = await Promise.all([
      videoInfoPromise,
      //this.translationsPromise,
      //captionsPromise,
      //this.configPromise,
      //this.PeertubePlayerManagerModulePromise
    ]);


    //await this.ensurePluginsAreLoaded(config, serverTranslations)

    const videoInfo: VideoDetails = videoInfoTmp;

    //const PeertubePlayerManager = PeertubePlayerManagerModule.PeertubePlayerManager
    // const videoCaptions = {}// await this.buildCaptions(serverTranslations, captionsResponse)

    this.loadParams(videoInfo);

    const playlistPlugin = this.currentPlaylistElement
      ? {
          elements: this.playlistElements,
          playlist: this.playlist,

          getCurrentPosition: () => this.currentPlaylistElement.position,

          onItemClicked: (videoPlaylistElement: VideoPlaylistElement) => {
            this.currentPlaylistElement = videoPlaylistElement;

            this.loadVideoAndBuildPlayer(
              this.currentPlaylistElement.video.uuid
            ).catch((err) => console.error(err));
          },
        }
      : undefined;

    const options: PeertubePlayerManagerOptions = {
      common: {
        // Autoplay in playlist mode
        autoplay: alreadyHadPlayer ? true : this.autoplay,
        controls: this.controls,
        muted: this.muted,
        loop: this.loop,

        captions: false, //videoCaptions.length !== 0,
        subtitle: this.subtitle,

        startTime: this.playlist
          ? this.currentPlaylistElement.startTimestamp
          : this.startTime,
        stopTime: this.playlist
          ? this.currentPlaylistElement.stopTimestamp
          : this.stopTime,

        nextVideo: this.playlist ? () => this.playNextVideo() : undefined,
        hasNextVideo: this.playlist
          ? () => !!this.getNextPlaylistElement()
          : undefined,

        previousVideo: this.playlist
          ? () => this.playPreviousVideo()
          : undefined,
        hasPreviousVideo: this.playlist
          ? () => !!this.getPreviousPlaylistElement()
          : undefined,

        playlist: playlistPlugin,

        videoCaptions: [],
        inactivityTimeout: 2500,
        videoViewUrl: this.host + this.getVideoUrl(videoInfo.uuid) + "/views",
        videoUUID: videoInfo.uuid,

        isLive: videoInfo.isLive,

        playerElement: this.playerElement,
        onPlayerElementChange: (element: HTMLVideoElement) =>
          (this.playerElement = element),

        videoDuration: videoInfo.duration,
        enableHotkeys: true,
        peertubeLink: this.peertubeLink,
        poster: this.wautoplay ? null : this.host + videoInfo.previewPath,
        theaterButton: false,

        serverUrl: this.host,
        language: this.language,
        embedUrl: this.host + videoInfo.embedPath,
        embedTitle: videoInfo.name,
      },

      webtorrent: {
        videoFiles: videoInfo.files,
      },
    };

    if (this.mode === "p2p-media-loader") {
      const hlsPlaylist = videoInfo.streamingPlaylists.find(
        (p) => p.type === VideoStreamingPlaylistType.HLS
      );

      Object.assign(options, {
        p2pMediaLoader: {
          playlistUrl: hlsPlaylist.playlistUrl,
          segmentsSha256Url: hlsPlaylist.segmentsSha256Url,
          redundancyBaseUrls: hlsPlaylist.redundancies.map((r) => r.baseUrl),
          trackerAnnounce: videoInfo.trackerUrls,
          videoFiles: hlsPlaylist.files,
        } as P2PMediaLoaderOptions,
      });
    }

    this.player = await PeertubePlayerManager.initialize(
      this.mode,
      options,
      (player: videojs.Player) => {
        this.player = player;
      }
    );

    this.player.on("customError", (event: any, data: any) =>
      this.handleError(data.err /*, serverTranslations*/)
    );

    const overlayString = this.isVideoEmbed
      ? `<a class="icon icon-only-logo-transparent" href="https://pocketnet.app/index?s=${this.txid}"></a>`
      : '<span class="icon icon-full-logo-transparent"></span>';

    this.player.overlay({
      overlays: [
        {
          content: overlayString,
          align: "top-left",
          start: 0,
          showBackground: false,
          class: "pocketnet-logo-video-player",
        },
      ],
    });

    this.initializeApi();

    this.removePlaceholder();

    this.runHook("action:embed.player.loaded", undefined, {
      player: this.player,
      videojs,
      video: videoInfo,
    });
  }

  private async initCore(videoId: string, parameters: any) {
    if (this.userTokens) this.setHeadersFromTokens();

    if(this.contributor != 'peertube') return this.buildVideoPlayerContributos(videoId)

    return this.loadVideoAndBuildPlayer(videoId);
  }

  private handleError(err: Error, translations?: { [id: string]: string }) {
    if (err.message.indexOf("from xs param") !== -1) {
      this.player.dispose();
      this.playerElement = null;
      this.displayError(
        "This video is not available because the remote instance is not responding.",
        translations
      );
      return;
    }
  }

  private async buildDock(videoInfo: VideoDetails, config: ServerConfig) {
    if (!this.controls) return;

    // On webtorrent fallback, player may have been disposed
    if (!this.player.player_) return;

    const title = this.title ? videoInfo.name : undefined;

    const description =
      config.tracker.enabled && this.warningTitle
        ? '<span class="text">' +
          peertubeTranslate(
            "Watching this video may reveal your IP address to others."
          ) +
          "</span>"
        : undefined;

    if (title || description) {
      this.player.dock({
        title,
        description,
      });
    }
  }

  private buildCSS() {
    const body = document.getElementById("custom-css");

    if (this.bigPlayBackgroundColor) {
      body.style.setProperty(
        "--embedBigPlayBackgroundColor",
        this.bigPlayBackgroundColor
      );
    }

    if (this.foregroundColor) {
      body.style.setProperty("--embedForegroundColor", this.foregroundColor);
    }
  }

  private async buildCaptions(
    serverTranslations: any,
    captionsResponse: Response
  ): Promise<VideoJSCaption[]> {
    if (captionsResponse.ok) {
      const {
        data,
      } = (await captionsResponse.json()) as ResultList<VideoCaption>;

      return data.map((c) => ({
        label: peertubeTranslate(c.language.label, serverTranslations),
        language: c.language.id,
        src: this.host + c.captionPath,
      }));
    }

    return [];
  }

  private loadPlaceholder(video: VideoDetails) {
    const placeholder = this.getPlaceholderElement();

    if (placeholder) {
      const url = this.host + video.previewPath;
      placeholder.style.backgroundImage = `url("${url}")`;


      if (!this.wautoplay) placeholder.style.display = "block";
      else {
        placeholder.style.display = "none";

      }
    }
  }

  private removePlaceholder() {
    const placeholder = this.getPlaceholderElement();

    if (placeholder) placeholder.style.display = "none";
  }

  private getPlaceholderElement() {
    return document.getElementById("placeholder-preview");
  }

  private setHeadersFromTokens() {
    this.headers.set(
      "Authorization",
      `${this.userTokens.tokenType} ${this.userTokens.accessToken}`
    );
  }

  private removeTokensFromHeaders() {
    this.headers.delete("Authorization");
  }

  /*private getResourceId () {
    const params = new URL(window.location.toString()).searchParams

    return this.getParamString(params, 'id', null)
  }*/

  private isPlaylistEmbed() {
    return window.location.pathname.split("/")[1] === "video-playlists";
  }

  private async ensurePluginsAreLoaded(
    config: ServerConfig,
    translations?: { [id: string]: string }
  ) {
    if (config.plugin.registered.length === 0) return;

    for (const plugin of config.plugin.registered) {
      for (const key of Object.keys(plugin.clientScripts)) {
        const clientScript = plugin.clientScripts[key];

        if (clientScript.scopes.includes("embed") === false) continue;

        const script = `/plugins/${plugin.name}/${plugin.version}/client-scripts/${clientScript.script}`;

        if (this.loadedScripts.has(script)) continue;

        const pluginInfo = {
          plugin,
          clientScript: {
            script,
            scopes: clientScript.scopes,
          },
          pluginType: PluginType.PLUGIN,
          isTheme: false,
        };

        await loadPlugin({
          hooks: this.peertubeHooks,
          pluginInfo,
          onSettingsScripts: () => undefined,
          peertubeHelpersFactory: (_) =>
            this.buildPeerTubeHelpers(translations),
        });
      }
    }
  }

  private buildPeerTubeHelpers(translations?: {
    [id: string]: string;
  }): RegisterClientHelpers {
    function unimplemented(): any {
      throw new Error("This helper is not implemented in embed.");
    }

    return {
      getBaseStaticRoute: unimplemented,

      getSettings: unimplemented,

      isLoggedIn: unimplemented,

      notifier: {
        info: unimplemented,
        error: unimplemented,
        success: unimplemented,
      },

      showModal: unimplemented,

      getServerConfig: unimplemented,

      markdownRenderer: {
        textMarkdownToHTML: unimplemented,
        enhancedMarkdownToHTML: unimplemented,
      },

      translate: (value: string) => {
        return Promise.resolve(peertubeTranslate(value, translations));
      },
    };
  }

  private runHook<T>(
    hookName: ClientHookName,
    result?: T,
    params?: any
  ): Promise<T> {
    return runHook(this.peertubeHooks, hookName, result, params);
  }
}

